<template>
  <div id="app">
    <transition name="fade" mode="out-in">
      <router-view/>  
    </transition>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style lang="stylus" scoped>
#app
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  height:100%;
.fade-enter-active,
.fade-leave-active
  transition: all .2s ease;
.fade-enter,
.fade-leave-active
  opacity: 0;
</style>



